#page-selecionar-ubs {
    width: 100%;
    height: 100vh;

    .contentWrapper {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        height: calc(100% - 25.2vh);

        background: #ffffff;
        flex: 1;
        padding: 32px;

        color: var(--gray-800);

        .container-titulo {
            h2 {
                font-size: 1.5rem;
                font-weight: 700;
                color: #361949;
            }
        }

        .container-escolher-ubs {
            .container-municipio {
                display: flex;
                align-items: center;
                justify-content: center;

                .btn-primary {
                    width: 276px;
                    height: 44px;

                    background-color: #361949;
                    border-radius: 10px;

                    font-size: 1.3rem;
                    font-weight: 500;
                    color: #ffffff;
                    margin: 0;
                }
            }

            .container-combo-ubs {
                .combo-ubs {
                    width: 276px;
                    height: 44px;
                    background-color: #53efbf;
                    color: #361949;
                    font-size: 1.3rem;
                    font-weight: 500;
                    text-align: center;
                    border: none;
                    border-radius: 0.25rem;
                    cursor: pointer;

                    scrollbar-width: auto;
                    scrollbar-color: #00e66f #ffffff;

                    option {
                        background-color: #fff;
                        font-size: 1rem;
                        font-weight: 400;
                        color: #361949;
                        text-align: left;
                        text-shadow: 0 1px 0 rgba(0, 0, 0, 0.4);
                    }
                }


                /* Chrome, Edge, and Safari */
                .combo-ubs ::-webkit-scrollbar {
                    width: 16px;
                }

                .combo-ubs ::-webkit-scrollbar-track {
                    background: #ffffff;
                }

                .combo-ubs ::-webkit-scrollbar-thumb {
                    background-color: #00e66f;
                    border-radius: 10px;
                    border: 3px solid #ffffff;
                }
            }
        }
    }
}
