* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

:root {
    --white: #FFFFFF;

    --gray-50: #F5F5F5;
    --gray-55: #FAFAFA;
    --gray-100: #CCCCCC;
    --gray-200: #b6acbd;
    --gray-800: #361949;

    --teal-300: #53EFBF;
    --teal-600: #1A8162;
}

@media (max-width: 1080px) {
    html {
        font-size: 93.75%;
    }
}

@media (max-width: 720px) {
    html {
        font-size: 87.5%;
    }
}

body {
    background: var(--gray-800);
    color: var(--white);
}

body,
input,
textarea,
select,
button {
    font: 400 1rem "Roboto", sans-serif;
}

button {
    cursor: pointer;
}

a {
    color: inherit;
    text-decoration: none;
}
