#page-painel {
    width: 100%;
    height: 100vh;

    .contentWrapperLoading {
        display: flex;
        align-items: center;
        justify-content: center;
        height: calc(100% - 25.2vh);
        background: #ffffff;
        color: var(--gray-800);
    }

    .contentWrapper {
        display: flex;
        align-items: center;
        flex-direction: column;
        background: #ffffff;
        flex: 1;
        padding: 32px;

        color: var(--gray-800);

        .linha {
            width: 49px;
            height: 5px;
            background-color: #361949;
            opacity: 1;
        }

        .container-trimestre {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            background-color: #361949;
            border-radius: 20px;
            margin: 0 16px;
            padding: 16px 32px;

            .titulo {
                font-size: 1.125rem;
                font-weight: 700;
                color: #ffffff;
            }

            .separador {
                width: 36px;
                height: 2px;
                background-color: #ffffff;
                opacity: 1;
            }

            .total-trimestre {
                font-size: 2.5rem;
                font-weight: 700;
                color: #ffffff;
            }
        }

        .container-semana {
            margin: 0 16px;
            padding: 8px 16px;
            border: 1px solid #361949;
            border-radius: 5px;

            font-size: 1.125rem;
            font-weight: 700;
        }

        h3 {
            font-size: 1.875rem;
            font-weight: 700;
            color: #361949;
        }

        .situacao-exames {
            .bordas {
                border-radius: 5px;
                border: 1px solid #361949b6;
            }
            .tipo {
                font-size: 1.125rem;
                font-weight: 700;
                color: #361949;
            }
        }

        .painel-secundario {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;

            .btn-primary {
                width: 408px;
            }

            .btn-secondary {
                width: 349px;
                height: 38px;
                background-color: #361949;
                border-radius: 5px;

                font-size: 1.125rem;
                font-weight: 700;
                color: #ffffff;
                margin: 0;
            }
        }

        .btn-light {
            width: 278px;

            background-color: #ffffff;
            border-radius: 5px;
            border: 1px solid #361949;

            font-size: 1.125rem;
            font-weight: 700;
            color: #361949;
        }

        .btn-primary {
            width: 391px;
            height: 38px;
            background-color: #53efbf;
            border-radius: 5px;
            margin: 0;
            padding: 0 32px;
            border: none;

            font-size: 1.125rem;
            font-weight: 700;
            color: #361949;
        }
    }
}
